import React from 'react';
import styled from 'styled-components';

const EmptyResultContainer = styled.div`
    width: 100%;
    text-align: center;
    padding: 16px 0;
    line-height: 1.3;
    color: var(--highlight);
`;

const EmptyResult = () => {
    return (
        <EmptyResultContainer>
            <h2>! </h2>
              .
            <br />
               .
        </EmptyResultContainer>
    );
};

export default React.memo(EmptyResult);
